      <div class="img" v-for="(item, index) in imgs" :key="index" :style="{backgroundImage: `url(${item})`}"></div>
    </div>
    <div class="bottom">
      <p>{{ comment.ordergoods.color + (comment.ordergoods.size || '') }}</p>
      <p v-if="false">{{ comment.like }} <van-icon class="like-icon" :color="comment.is_like ? '#FF8B3E' : '#9B9B9D'" name="good-job-o" /></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'comment-item',
  props: {
    comment: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed: {
    imgs() {
      return this.comment.img_urls.split(',')
    }
  }
}
