<template>
  <div class="goods-comment" id="goods_comment">
    <back-header title="商品评价"/>
    <scroll @scrollToEnd="loadMore" clickable :is-finished="finished" ref="scroll" class="scroll">
      <ul class="tabs">
        <li @click="changeTab(0)" :class="{active: active===0}">全部（{{ total1 }}）</li>
        <li @click="changeTab(1)" :class="{active: active===1}">好评（{{ total2 }}）</li>
        <li @click="changeTab(2)" :class="{active: active===2}">有图（{{ total3 }}）</li>
      </ul>
      <div class="list">
        <comment-item v-for="item in list" :key="item.id" :comment="item" />
      </div>
    </scroll>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import CommentItem from '../../components/comment-item/comment-item'
import { post } from '../../common/js/http'

const PAGE_SZIE = 10

export default {
  name: 'goods-comment',
  components: {
    BackHeader,
    Scroll,
    CommentItem
  },
  data() {
    return {
      active: 0,
      list: [],
      finished: true,
      loading: false,
      total1: 0,
      total2: 0,
      total3: 0,
      page: 1
    }
  },
  created() {
    this.getList()
  },
  methods: {
    changeTab(tab) {
      if (tab === this.active) return
      this.active = tab
      this.page = 1
      this.getList()
    },
    getList(fullloading = true) {
      post('/shop/evaluate/goodsEvaluateList', {
        goodsid: this.$route.params.id,
        type: this.active,
        page: this.page,
        size: PAGE_SZIE
      })
        .then(({ data: { data } }) => {
          this.total1 = data.total_count
          this.total2 = data.good_count
          this.total3 = data.img_count
          this.list = this.page === 1 ? data.list : this.list.concat(data.list)
          this.loading = false
          this.finished = data.list.length < PAGE_SZIE
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        })
    },
    loadMore() {
      if (this.loading) return
      this.page++
      this.loading = true
      this.getList(false)
    }
  }
}
</script>

